"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { api } from "@/lib/axios";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import {
  Card,
  CardHeader,
  CardTitle,
  CardDescription,
  CardContent,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs";
import {
  Plus,
  Search,
  FileText,
  Clock,
  AlertCircle,
  Sparkles,
  Zap,
  History,
  MousePointer2,
  Star,
} from "lucide-react";
import Link from "next/link";
import { Skeleton } from "@/components/ui/skeleton";

type Note = {
  id: string;
  title: string;
  updatedAt: string;
  previewText?: string;
};

export default function DashboardPage() {
  const [notes, setNotes] = useState<Note[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [searchQuery, setSearchQuery] = useState("");

  const router = useRouter();
  const [isCreating, setIsCreating] = useState(false);

  useEffect(() => {
    async function fetchNotes() {
      try {
        setLoading(true);
        const res = await api.get("/documents");
        setNotes(Array.isArray(res.data) ? res.data : []);
      } catch {
        setError("Could not load notes. Is the backend running?");
      } finally {
        setLoading(false);
      }
    }
    fetchNotes();
  }, []);

  // âœ… FIXED HERE
  const handleCreateNote = async () => {
    try {
      setIsCreating(true);

      const res = await api.post("/documents", {
        title: "Untitled Document",
        content: "",
      });

      router.push(`/dashboard/documents/${res.data.id}`);
    } catch (err) {
      console.error("Failed to create note", err);
      setError("Failed to create document");
    } finally {
      setIsCreating(false);
    }
  };

  const filteredNotes = notes.filter((note) =>
    note.title.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
    <div className="container mx-auto px-6 py-8 max-w-7xl">
      <div className="flex flex-col md:flex-row md:items-center justify-between gap-6 mb-10">
        <div className="space-y-1">
          <div className="flex items-center gap-2 mb-2">
            <Badge variant="outline" className="rounded-full border-primary/30 text-primary bg-primary/5 px-3">
              <Sparkles className="h-3 w-3 mr-1" /> 2025 Workspace
            </Badge>
            <Badge variant="outline" className="rounded-full border-muted-foreground/20 text-muted-foreground px-3">
              Free Tier
            </Badge>
          </div>
          <h1 className="text-4xl font-bold tracking-tight bg-linear-to-r from-foreground to-foreground/70 bg-clip-text">
            Your Hub
          </h1>
          <p className="text-muted-foreground text-md">
            Collaborate on documents and manage AI-powered notes.
          </p>
        </div>

        <div className="flex items-center gap-3">
          <Button variant="outline" size="lg" className="rounded-xl border-dashed">
            <Star className="h-4 w-4 mr-2" /> Favorites
          </Button>
          <Button
            onClick={handleCreateNote}
            disabled={isCreating}
            size="lg"
            className="rounded-xl shadow-lg shadow-primary/20 gap-2 px-6"
          >
            <Plus className="h-5 w-5" />
            {isCreating ? "Creating..." : "New Note"}
          </Button>
        </div>
      </div>

      <div className="grid gap-6 sm:grid-cols-2">
        {loading ? (
          Array.from({ length: 4 }).map((_, i) => (
            <Skeleton key={i} className="h-50 w-full rounded-3xl" />
          ))
        ) : filteredNotes.length > 0 ? (
          filteredNotes.map((note) => (
            <Link key={note.id} href={`/dashboard/documents/${note.id}`}>
              <Card className="group h-full hover:shadow-xl transition rounded-3xl">
                <CardHeader>
                  <CardTitle>{note.title || "Untitled"}</CardTitle>
                  <CardDescription className="flex items-center gap-1">
                    <Clock className="h-3 w-3" />
                    {new Date(note.updatedAt).toLocaleDateString()}
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <p className="text-sm text-muted-foreground line-clamp-2">
                    {note.previewText || "Click to open editor"}
                  </p>
                </CardContent>
              </Card>
            </Link>
          ))
        ) : (
          <div className="col-span-full py-24 text-center">
            <Zap className="mx-auto h-12 w-12 opacity-30" />
            <p>No documents yet</p>
          </div>
        )}
      </div>
    </div>
  );
}
